/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
// hooks
import useResponsive from '../../hooks/useResponsive';

// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Switch,
    Tabs,
    Button,
    Tab,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Stack,
    Box,
    Avatar,
    Divider
} from '@mui/material';
import {
    Wifi,
    Phone,
    Tv,
    Description,
    CreditCard,
    Receipt,
    AccountBalanceWallet,
    FlashOn,
    School
} from '@mui/icons-material';

// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number
import { fCurrency } from '../../utils/formatNumber';
// sections
import { TransHead, PlanToolbar } from '../../sections/admin/user/list';
// axios
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'recipients', label: 'Transaction Details', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'amount', label: 'Amount', alignRight: true },
    { id: 'action', label: 'Action', alignRight: true }
];

const ACCOUNT_TABS = [
    { value: 'ALL', label: 'ALL' },
    { value: 1, label: 'success' },
    { value: 0, label: 'processing' },
    { value: 2, label: 'fail' }
];

const TRANSACTION_TYPES = [
    { value: 'all', label: 'All Transactions', endpoint: 'history' },
    { value: 'data', label: 'Data Transactions', endpoint: 'datatrans' },
    { value: 'airtime', label: 'Airtime Transactions', endpoint: 'airtime/trans' },
    { value: 'cable', label: 'Cable TV Transactions', endpoint: 'cable/trans' },
    { value: 'bill', label: 'Bill Payment', endpoint: 'bill/trans' },
    { value: 'deposit', label: 'Deposit Transactions', endpoint: 'deposit/trans' },
    { value: 'airtimecash', label: 'Airtime 2 Cash', endpoint: 'airtimecash/trans' },
    { value: 'result', label: 'Result Checker', endpoint: 'result/trans' },
    { value: 'data_card', label: 'Data Card', endpoint: 'data_card' },
    { value: 'recharge_card', label: 'Recharge Card', endpoint: 'recharge_card' }
];

// ----------------------------------------------------------------------

export default function Transactions() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const isMobile = useResponsive('down', 'sm');
    const [userList, setUserList] = useState([]);
    const [page, setPage] = useState(0);
    const [load, SetLoad] = useState(true);
    const [totalPage, SetTotal] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [dense, Setdense] = useState(false);
    const [isNotFound, SetNotFound] = useState(false);
    const [status, setStatus] = useState('ALL');
    const [transactionType, setTransactionType] = useState('all');
    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        initialize(page, rowsPerPage, status, filterName, transactionType);
    }, []);

    const initialize = async (pag, Habukhan = 10, statusFilter, search, type) => {
        let Habukhan_page = pag + 1;
        SetLoad(true);

        try {
            let response;
            let transData;

            if (type === 'all') {
                response = await axios.get(
                    `/api/system/all/history/records/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.all_summary;
            } else {
                const config = TRANSACTION_TYPES.find(t => t.value === type);
                response = await axios.get(
                    `/api/system/all/${config.endpoint}/habukhan/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                const dataKey = Object.keys(response.data).find(key => key.includes('_trans') || key.includes('data_card') || key.includes('recharge_card'));
                transData = response.data[dataKey];
            }

            setUserList(transData?.data || []);
            SetTotal(transData?.total || 0);
            SetLoad(false);
            setPage(pag);
            SetNotFound((transData?.total || 0) === 0);
        } catch (error) {
            console.error('Error fetching transactions:', error);
            SetLoad(false);
            setUserList([]);
            SetNotFound(true);
        }
    };

    const handleChangeRowsPerPage = (event) => {
        const value = parseInt(event.target.value, 10);
        setRowsPerPage(value);
        initialize(0, value, status, filterName, transactionType);
        setPage(0);
    };

    const handleFilterByName = async (val) => {
        setFilterName(val);
        setPage(0);
        initialize(0, rowsPerPage, status, val, transactionType);
    };

    const handleTransactionTypeChange = (event) => {
        const newType = event.target.value;
        setTransactionType(newType);
        setPage(0);
        initialize(0, rowsPerPage, status, filterName, newType);
    };

    return (
        <Page title="Transactions">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Transaction Summary"
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
                        { name: 'List' },
                    ]}
                />

                <Card>
                    <Box sx={{ p: 2, display: 'flex', gap: 2, alignItems: 'center' }}>
                        <FormControl sx={{ minWidth: 200 }} size="small">
                            <InputLabel>Category</InputLabel>
                            <Select
                                value={transactionType}
                                label="Category"
                                onChange={handleTransactionTypeChange}
                            >
                                {TRANSACTION_TYPES.map((type) => (
                                    <MenuItem key={type.value} value={type.value}>
                                        {type.label}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                        <Tabs
                            value={status}
                            scrollButtons="auto"
                            variant="scrollable"
                            allowScrollButtonsMobile
                            onChange={(e, value) => {
                                setStatus(value);
                                initialize(0, rowsPerPage, value, filterName, transactionType);
                            }}
                        >
                            {ACCOUNT_TABS.map((tab) => (
                                <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                            ))}
                        </Tabs>
                    </Box>

                    <PlanToolbar filterName={filterName} onFilterName={handleFilterByName} />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table size={dense ? 'small' : 'medium'}>
                                <TransHead
                                    order={'asc'}
                                    orderBy={'id'}
                                    headLabel={TABLE_HEAD}
                                    rowCount={userList.length}
                                />

                                {!load ? (
                                    <TableBody>
                                        {userList.map((row, index) => {
                                            const {
                                                transid,
                                                amount,
                                                oldbal,
                                                newbal,
                                                plan_status,
                                                plan_name,
                                                plan_phone,
                                                sender_number,
                                                message,
                                                plan_date,
                                                habukhan_date,
                                                Habukhan_date,
                                                adex_date,
                                                date,
                                                iuc,
                                                meter_number,
                                                phone,
                                                phone_number,
                                                phone_account,
                                                account_number,
                                                numbers,
                                                name,
                                                exam_name,
                                                disco_name,
                                                bank_name,
                                                created_at,
                                                cable_name,
                                                role,
                                                network
                                            } = row;

                                            // Use phone_account field (new) or dedicated fields, with regex fallback for old data
                                            let beneficiary = phone_account || plan_phone || phone || phone_number || sender_number || iuc || meter_number || account_number || numbers;

                                            // If still no beneficiary, try extracting from message (for old transactions)
                                            if (!beneficiary && message) {
                                                const phoneMatch = message.match(/\d{10,11}/);
                                                if (phoneMatch) {
                                                    beneficiary = phoneMatch[0];
                                                }
                                            }

                                            beneficiary = beneficiary || 'N/A';
                                            const actualDate = plan_date || Habukhan_date || habukhan_date || adex_date || created_at || date || 'N/A';
                                            const details = plan_name || name || exam_name || disco_name || bank_name || cable_name || message || 'Service Purchase';

                                            let viewType = transactionType;
                                            if (viewType === 'all') {
                                                if (role) {
                                                    viewType = role;
                                                } else {
                                                    const msg = message ? message.toLowerCase() : '';

                                                    // Fallback heuristics for older transactions
                                                    if (cable_name || msg.includes('dstv') || msg.includes('gotv') || msg.includes('startimes')) viewType = 'cable';
                                                    else if (disco_name || msg.includes('meter') || msg.includes('electricity') || msg.includes('disco')) viewType = 'bill';
                                                    else if (exam_name || msg.includes('waec') || msg.includes('neco') || msg.includes('scratch card')) viewType = 'result';
                                                    else if (msg.includes('airtime to cash') || msg.includes('convert to cash') || msg.includes('airtime 2 cash')) viewType = 'airtimecash';
                                                    else if (msg.includes('funding') || msg.includes('wallet') || msg.includes('deposit')) viewType = 'deposit';
                                                    else if (
                                                        plan_name ||
                                                        msg.includes('data') ||
                                                        msg.includes('gifted') ||
                                                        msg.includes('shared') ||
                                                        msg.includes('sme') ||
                                                        msg.includes('gifting') ||
                                                        msg.includes('corporate')
                                                    ) {
                                                        viewType = 'data';
                                                    }
                                                    else if (
                                                        msg.includes('airtime') ||
                                                        msg.includes('vtu') ||
                                                        msg.includes('sns') ||
                                                        msg.includes('recharge')
                                                    ) {
                                                        viewType = 'airtime';
                                                    }
                                                    else {
                                                        // If ambiguous, check simply for network presence or keywords
                                                        if (msg.includes('purchase') && network) viewType = 'airtime';
                                                        else viewType = 'data';
                                                    }
                                                }
                                            }

                                            // Normalize Map
                                            if (viewType === 'exam') viewType = 'result';
                                            if (viewType === 'credit' || viewType === 'momo') viewType = 'deposit';
                                            if (viewType === 'manual') viewType = 'manual';
                                            if (viewType === 'cash') viewType = 'airtimecash';

                                            // Whitelist validation to prevent 404s
                                            const VALID_TYPES = ['data', 'airtime', 'cable', 'bill', 'deposit', 'airtimecash', 'result', 'manual', 'bulksms', 'data_card', 'recharge_card'];
                                            if (!VALID_TYPES.includes(viewType)) {
                                                viewType = 'data';
                                            }

                                            let Icon = Description;
                                            if (viewType === 'data' || viewType === 'datatrans') Icon = Wifi;
                                            if (viewType === 'airtime' || viewType === 'airtime/trans') Icon = Phone;
                                            if (viewType === 'cable' || viewType === 'cable/trans') Icon = Tv;
                                            if (viewType === 'bill' || viewType === 'bill/trans') Icon = FlashOn;
                                            if (viewType === 'result' || viewType === 'result/trans') Icon = School;
                                            if (viewType === 'deposit' || viewType === 'fund' || viewType === 'wallet') Icon = AccountBalanceWallet;
                                            if (viewType === 'airtimecash' || viewType === 'cash') Icon = CreditCard;

                                            const isCredit = viewType === 'deposit' || viewType === 'fund' || viewType === 'wallet' || viewType === 'airtimecash';

                                            if (isMobile) {
                                                return (
                                                    <TableRow key={transid || index}>
                                                        <TableCell sx={{ p: 2, display: 'block', width: '100%' }}>
                                                            <Card sx={{ p: 2, bgcolor: theme.palette.mode === 'light' ? 'background.neutral' : 'background.paper' }}>
                                                                <Stack direction="row" alignItems="center" justifyContent="space-between" spacing={2} sx={{ mb: 2 }}>
                                                                    <Stack direction="row" alignItems="center" spacing={2}>
                                                                        <Avatar sx={{ bgcolor: theme.palette.mode === 'light' ? '#F4F6F8' : '#333', color: theme.palette.primary.main }}>
                                                                            <Icon />
                                                                        </Avatar>
                                                                        <Box>
                                                                            <Typography variant="subtitle2" noWrap sx={{ maxWidth: 180 }}>
                                                                                {details || 'Transaction'}
                                                                            </Typography>
                                                                            <Typography variant="caption" sx={{ color: 'text.secondary', display: 'block' }}>
                                                                                {actualDate}
                                                                            </Typography>
                                                                        </Box>
                                                                    </Stack>
                                                                    <Label
                                                                        variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                                        color={(plan_status === 1 ? 'success' : (plan_status === 2 ? 'error' : 'warning'))}
                                                                    >
                                                                        {sentenceCase((plan_status === 0 ? 'Wait' : (plan_status === 1 ? 'Success' : 'Failed')))}
                                                                    </Label>
                                                                </Stack>

                                                                <Divider sx={{ borderStyle: 'dashed', mb: 2 }} />

                                                                <Stack direction="row" alignItems="center" justifyContent="space-between">
                                                                    <Stack>
                                                                        <Typography variant="caption" sx={{ color: 'text.secondary' }}>Amount</Typography>
                                                                        <Typography variant="subtitle1" sx={{ fontWeight: 'bold', color: isCredit ? 'success.main' : 'text.primary' }}>
                                                                            {isCredit ? '+' : '-'}₦{fCurrency(amount)}
                                                                        </Typography>
                                                                    </Stack>
                                                                    <Button
                                                                        size="small"
                                                                        variant="outlined"
                                                                        component={RouterLink}
                                                                        to={`${PATH_DASHBOARD.general.invioce}/${transid}/${viewType}`}
                                                                        state={{
                                                                            transaction: {
                                                                                ...row,
                                                                                beneficiary,
                                                                                plan_name: details,
                                                                                network: network || (() => {
                                                                                    const d = details ? details.toLowerCase() : '';
                                                                                    const p = beneficiary ? beneficiary.toString() : '';
                                                                                    if (d.includes('mtn')) return 'MTN';
                                                                                    if (d.includes('airtel')) return 'Airtel';
                                                                                    if (d.includes('glo')) return 'Glo';
                                                                                    if (d.includes('9mobile') || d.includes('etisalat')) return '9mobile';
                                                                                    if (d.includes('dstv')) return 'DSTV';
                                                                                    if (d.includes('gotv')) return 'GOTV';
                                                                                    if (d.includes('startimes')) return 'Startimes';
                                                                                    if (d.includes('electric') || d.includes('power')) return 'Electricity';
                                                                                    if (d.includes('waec') || d.includes('neco')) return 'WAEC/NECO';
                                                                                    if (d.includes('gifted') || d.includes('sme') || d.includes('share') || d.includes('corporate')) return 'MTN';
                                                                                    if (p.startsWith('0803') || p.startsWith('0806') || p.startsWith('0703') || p.startsWith('0706') || p.startsWith('0813') || p.startsWith('0816') || p.startsWith('0810') || p.startsWith('0814') || p.startsWith('0903') || p.startsWith('0906') || p.startsWith('0913') || p.startsWith('0916') || p.startsWith('0704')) return 'MTN';
                                                                                    if (p.startsWith('0802') || p.startsWith('0808') || p.startsWith('0708') || p.startsWith('0812') || p.startsWith('0701') || p.startsWith('0902') || p.startsWith('0907') || p.startsWith('0901') || p.startsWith('0912') || p.startsWith('0904')) return 'Airtel';
                                                                                    if (p.startsWith('0805') || p.startsWith('0807') || p.startsWith('0705') || p.startsWith('0815') || p.startsWith('0811') || p.startsWith('0905') || p.startsWith('0915')) return 'Glo';
                                                                                    if (p.startsWith('0809') || p.startsWith('0818') || p.startsWith('0817') || p.startsWith('0909') || p.startsWith('0908')) return '9mobile';
                                                                                    return 'N/A';
                                                                                })(),
                                                                                plan_date: actualDate,
                                                                                category: viewType
                                                                            }
                                                                        }}
                                                                    >
                                                                        View Receipt
                                                                    </Button>
                                                                </Stack>
                                                            </Card>
                                                        </TableCell>
                                                    </TableRow>
                                                );
                                            }

                                            return (
                                                <TableRow hover key={transid || index} sx={{ '& td': { borderBottom: '1px dashed #eef2f6' } }}>
                                                    <TableCell align="left">
                                                        <Stack direction="row" alignItems="center" spacing={2}>
                                                            <Avatar sx={{ bgcolor: (theme) => theme.palette.mode === 'light' ? '#F4F6F8' : '#333', color: (theme) => theme.palette.primary.main }}>
                                                                <Icon />
                                                            </Avatar>
                                                            <Stack>
                                                                <Typography variant="subtitle2" noWrap sx={{ maxWidth: 240 }}>
                                                                    {details}
                                                                </Typography>
                                                                <Typography variant="caption" sx={{ color: 'text.secondary' }}>
                                                                    {actualDate} &nbsp;&bull;&nbsp; {beneficiary}
                                                                </Typography>
                                                            </Stack>
                                                        </Stack>
                                                    </TableCell>

                                                    <TableCell align="left">
                                                        <Label
                                                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                            color={(plan_status === 1 ? 'success' : (plan_status === 2 ? 'error' : 'warning'))}
                                                        >
                                                            {sentenceCase((plan_status === 0 && 'processing') || (plan_status === 1 && 'successful') || (plan_status === 2 && 'failed'))}
                                                        </Label>
                                                    </TableCell>

                                                    <TableCell align="right">
                                                        <Typography variant="subtitle1" sx={{ fontWeight: 'bold', color: isCredit ? 'success.main' : 'text.primary' }}>
                                                            {isCredit ? '+' : '-'}₦{fCurrency(amount)}
                                                        </Typography>
                                                    </TableCell>

                                                    <TableCell align="right">
                                                        <Button
                                                            size="small"
                                                            component={RouterLink}
                                                            to={`${PATH_DASHBOARD.general.invioce}/${transid}/${viewType}`}
                                                            state={{
                                                                transaction: {
                                                                    ...row,
                                                                    beneficiary,
                                                                    plan_name: details, // Map details to plan_name for the invoice
                                                                    network: network || (() => {
                                                                        const d = details ? details.toLowerCase() : '';
                                                                        const p = beneficiary ? beneficiary.toString() : '';

                                                                        // 1. Direct Keyword Match
                                                                        if (d.includes('mtn')) return 'MTN';
                                                                        if (d.includes('airtel')) return 'Airtel';
                                                                        if (d.includes('glo')) return 'Glo';
                                                                        if (d.includes('9mobile') || d.includes('etisalat')) return '9mobile';
                                                                        if (d.includes('dstv')) return 'DSTV';
                                                                        if (d.includes('gotv')) return 'GOTV';
                                                                        if (d.includes('startimes')) return 'Startimes';
                                                                        if (d.includes('electric') || d.includes('power')) return 'Electricity';
                                                                        if (d.includes('waec') || d.includes('neco')) return 'WAEC/NECO';

                                                                        // 2. Context Inference (Common Terms)
                                                                        if (d.includes('gifted') || d.includes('sme') || d.includes('share') || d.includes('corporate')) return 'MTN'; // "Gifted" usually implies MTN SME/Gifting

                                                                        // 3. Phone Number Prefix Inference
                                                                        if (p.startsWith('0803') || p.startsWith('0806') || p.startsWith('0703') || p.startsWith('0706') || p.startsWith('0813') || p.startsWith('0816') || p.startsWith('0810') || p.startsWith('0814') || p.startsWith('0903') || p.startsWith('0906') || p.startsWith('0913') || p.startsWith('0916') || p.startsWith('0704')) return 'MTN';
                                                                        if (p.startsWith('0802') || p.startsWith('0808') || p.startsWith('0708') || p.startsWith('0812') || p.startsWith('0701') || p.startsWith('0902') || p.startsWith('0907') || p.startsWith('0901') || p.startsWith('0912') || p.startsWith('0904')) return 'Airtel';
                                                                        if (p.startsWith('0805') || p.startsWith('0807') || p.startsWith('0705') || p.startsWith('0815') || p.startsWith('0811') || p.startsWith('0905') || p.startsWith('0915')) return 'Glo';
                                                                        if (p.startsWith('0809') || p.startsWith('0818') || p.startsWith('0817') || p.startsWith('0909') || p.startsWith('0908')) return '9mobile';
                                                                        return 'N/A';
                                                                    })(),
                                                                    plan_date: actualDate,
                                                                    category: viewType
                                                                }
                                                            }}
                                                            sx={{ textTransform: 'none', fontWeight: 'bold', whiteSpace: 'nowrap' }}
                                                        >
                                                            View Receipt
                                                        </Button>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                ) : (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={4} sx={{ py: 3 }}>
                                                <Typography variant="body2">Loading ...</Typography>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}

                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={4} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ p: 2 }}>
                        <FormControlLabel
                            control={<Switch checked={dense} onChange={(e) => Setdense(e.target.checked)} />}
                            label="Dense padding"
                        />
                        <TablePagination
                            rowsPerPageOptions={[5, 10, 25, 50, 100]}
                            component="div"
                            count={totalPage}
                            rowsPerPage={rowsPerPage}
                            page={page}
                            onPageChange={(e, p) => initialize(p, rowsPerPage, status, filterName, transactionType)}
                            onRowsPerPageChange={handleChangeRowsPerPage}
                        />
                    </Stack>
                </Card>
            </Container>
        </Page>
    );
}
